<template>
  <footer class="cart">
    <!-- 购物车详情页顶部模糊效果 -->
    <transition name='fade'>
      <div class="cartListMask" v-show="cartListShow">
      </div>
    </transition>

    <!-- 价格区块 -->
    <div class="price-wrapper">
      <!-- 购物车图标 -->
      <div v-on:click.stop.prevent="toggleCartListFold" class="icon-wrapper">
        <div class="icon">
          <span class="icon-shopping_cart" v-bind:class="{highlight:totalCount>0}"></span>
        </div>
        <div class="count" v-if="totalCount>0">
          {{totalCount}}
        </div>
      </div>
      <!-- 价格 -->
      <div class="price">
        <p v-bind:class="{highlight:totalPrice>0}"><span class="symbol">￥</span>{{totalPrice}}</p>
      </div>
      <span class="line"></span>
      <div class="other-cost">
        <p>另需配送费<span class="symbol">￥</span>{{seller.deliveryPrice}}元</p>
      </div>
    </div>
    <!-- 结算区块 -->
    <div class="checkout" v-bind:class="{active:totalPrice>=seller.minPrice}">
      <p>{{checkoutDesc}}</p>
    </div>

    <!-- 购物车详情页面 -->
    <div class="cartList" v-show="cartListShow">
      <div class="header">
        <h1 class="title">购物车</h1>
        <button v-on:click.stop.prevent="emptyCart(selectFoods)"
        type="button" name="button" class="emptyCart">清空</button>
      </div>
      <div class="content" ref="cartListHook">
        <ul>
          <li v-for="selectFood in selectFoods" class="selectFoodItem">
            <span class="foodName">{{selectFood.name}}</span>
            <div class="priceWrapper">
              <span class="foodPrice"><span class="symbol">￥</span>{{selectFood.price}}</span>
              <cartButton v-bind="{food:selectFood,
                goodIndex:selectFood.goodIndex,
                foodIndex:selectFood.foodIndex}">
              </cartButton>
            </div>
          </li>
        </ul>
      </div>
    </div>


  </footer>
</template>
<script>
  import {mapState,mapGetters,mapMutations} from 'vuex';
  import cartButton from '../cartButton/cartButton';
  import BScroll from "better-scroll";
  export default {
    name:"shopcart",
    data:function () {
      return {
        cartListFold:true,
      }
    },
    components:{
      "cartButton":cartButton,
    },
    computed:{
      ...mapState(['seller']),
      ...mapGetters(['selectFoods']),
      totalPrice:function () {
        let total=0;
        this.selectFoods.forEach(function (food) {
          total+=food.price*food.count
        })
        return total;
      },
      totalCount:function () {
        let count=0;
        this.selectFoods.forEach(function (food) {
          count+=food.count
        })
        return count;
      },
      checkoutDesc:function () {
        if (this.totalPrice === 0){
          return `￥${this.seller.minPrice}起送`
        }else if(this.totalPrice < this.seller.minPrice){
          return `还差￥${this.seller.minPrice-this.totalPrice}起送`
        }else{
          return '去结算'
        }
      },
      cartListShow:function () {
        if (this.selectFoods.length && !this.cartListFold){
          this.$nextTick( () => {
            if (!this.scroll){
              this.scroll=this._initScroll();
            }else{
              this.scroll.refresh();
            }
          });
          return true;
        }else {
          return false;
        }
      }
    },
    // created:function () {
    //   this.$store.dispatch('getSeller');
    // },
    methods:{
      ...mapMutations(['emptyCart']),
      toggleCartListFold:function () {
        this.cartListFold=!this.cartListFold;
      },
      _initScroll(){
        new BScroll(this.$refs.cartListHook,{click:true});
      }
    }

  }
</script>
<style scoped>
button{
  outline: none;
}
.cart {
  display: flex;
  min-height: 46px;
  position: relative;
  flex-wrap: wrap;
}
.cartListMask{
  position: fixed;
  z-index: 50;
  top: 0;
  bottom: 0;
  height: 100%;
  width: 100%;
  background-color: rgba(7, 17, 27, 0.6);
  filter: blur(10px);
}
.fade-enter-active, .fade-leave-active{
  opacity: 0.6;
  transition: all .3s linear;
}
.fade-enter, .fade-leave-to{
  opacity: 0
}
.cart .price-wrapper{
  display: flex;
  align-items: center;
  background-color: #000;
  flex: 1 0;
  z-index: 150;
}
.cart .price-wrapper .icon-wrapper{
  position: relative;
  display: inline-block;
  width: 44px;
  height: 44px;
  margin: 0 18px;

}
.cart .price-wrapper .icon-wrapper .count{
  position: absolute;
  top:-16px;
  right:-6px;
  width: 24px;
  height: 16px;
  line-height: 16px;
  border-radius: 12px;
  background-color: #f00;
  text-align: center;
  color: #fff;
}
.cart .price-wrapper .icon-wrapper .icon{
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  position: absolute;
  box-sizing: border-box;
  bottom: 8px;
  width: 44px;
  height: 44px;
  border: 6px solid #000;
  border-radius:50%;
  background-color: #2b343c;
}
.cart .price-wrapper .icon-wrapper .icon .icon-shopping_cart{
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  font-size: 20px;
  color: rgba(255, 255, 255,0.4);
  line-height: 20px;
  background-color: #2b343c;
  border-radius: 50%;
}
.cart .price-wrapper .icon-wrapper .icon .icon-shopping_cart.highlight{
  color: rgba(255, 255, 255, 1);
  background-color: #0f88f5;
}




.cart .price-wrapper .price{
  display: inline-block;
}
.cart .price-wrapper .price p{
  font-size: 16px;
  color: rgba(255, 255, 255,0.4);
  font-weight: 700;
  line-height: 24px;
  vertical-align: middle;
}
.cart .price-wrapper .price p.highlight{
  color: #fff;
}
.cart .price-wrapper .line{
  display: inline-block;
  height: 24px;
  width: 1px;
  background-color: rgba(255, 255, 255, 0.1);
  transform: scaleX(0.5);
  margin:0 12px;
  vertical-align: middle;
}
.cart .price-wrapper .other-cost{
  display: inline-block;
}
.cart .price-wrapper .other-cost p{
  font-size: 14px;
  color:rgba(255, 255, 255, 0.4);
  font-weight: 300;
  line-height: 24px;
}
.cart .checkout{
  z-index: 150;
  display: flex;
  align-items: center;
  justify-content: center;
  flex: 0 1 105px;
  font-size: 14px;
  background-color: rgba(45, 46, 59, 0.96);
  color: rgba(255, 255, 255, 0.4);
  line-height: 24px;
}
.cart .checkout.active{
  background-color: rgb(5, 124, 67);
  color: #fff;
}
.cart .checkout.active p{
  font-weight: 700;
}


/*购物车详情页*/
.cart .cartList {
  /*position: fixed;*/
  position: absolute;
  z-index: 100;
  box-sizing: border-box;
  width: 100%;
  bottom: 46px;
}
.cart .cartList .header{
  display: flex;
  justify-content: space-between;
  box-sizing: border-box;
  height: 40px;
  padding: 0 18px;
  border-bottom: 1px solid rgba(7, 17, 27,0.1 );
  background-color: #f3f5f7;

}
.cart .cartList .header .title{
  font-size: 14px;
  font-weight: 200;
  color: rgb(7, 17, 27);
  line-height: 40px;
}
.cart .cartList .header .emptyCart{
  font-size: 12px;
  color: rgb(0, 160, 220);
  line-height: 40px;
  border: none;
  background-color: rgba(0, 0, 0, 0);
}
.cart .cartList .content{
  max-height: 220px;
  overflow: hidden;
  padding: 0 18px;
  background-color: rgb(255, 255, 255);
}
.cart .cartList .content .selectFoodItem{
  display: flex;
  justify-content: space-between;
  box-sizing: border-box;
  height: 48px;
  background-color: rgb(255, 255, 255);
  border-bottom: 1px solid rgba(7, 17, 27, 0.1);
}
.cart .cartList .content .selectFoodItem .foodName{
  font-size: 14px;
  color: rgb(7, 17, 27);
  height: 48px;
  line-height: 48px;
}
.cart .cartList .content .selectFoodItem .priceWrapper{
  display: flex;
  flex-grow: 0;
}
.cart .cartList .content .selectFoodItem .foodPrice{
  margin-right: 12px;
  font-size: 14px;
  font-weight: 700;
  color: rgb(240, 20, 20);
  line-height: 48px;
}
.cart .cartList .content .selectFoodItem .foodPrice .symbol{
  font-size: 10px;
  font-weight: normal;
}
</style>



