<!-- 商品页面整体架构 -->
<template>
  <div class="goods">
    <div class="goods-wrapper">
      <goods-menu class="menu"></goods-menu>
      <goods-content class="content"></goods-content>
    </div>
    <shopcart class="cart"></shopcart>
  </div>
</template>
<script>
  import menu from './menu/menu.vue';
  import content from './content/content.vue';
  import shopcart from './shopcart/shopcart';
  export default {
    name:"goods",
    components:{
      goodsMenu:menu,
      goodsContent:content,
      shopcart:shopcart
    },
    data:function () {
      return {
        goods:{
          type:Array,
        }
      }
    },
  }
</script>

<style scoped>
  .goods-wrapper{
    display: flex;
    height: calc(100vh - 224px);
    overflow: hidden;
  }
  .goods-wrapper .menu{
    box-sizing: border-box;
    width: 80px;
    height: 100%;
    flex-shrink: 1;
  }
  .goods-wrapper .content{
    flex:1 0;
    /*宽度设置一个默认值，保证兼容性*/
    width: 40px;
    height: 100%;
    /*设置一个默认高度值，以使better-scroll生效*/
  }
  .cart{
    flex: 0 1;
    width: 100%;
    background-color: #141d27;
    height: 46px;
  }

</style>



