<template>
  <div class="menu" ref="menu">
    <ul>
      <li v-on:click.stop.prevent="menuSelect(index,$event)"
      v-for="(item,index) in goods" class="item item-hook" v-bind:class="{current:currentIndex===index}">
        <p class="text" v-bind:class="{current:currentIndex===index}">
          <icon v-bind="{iconNameVal:classMap[item.type],iconTypeVal:1}" v-if="item.type>0" class="icon"></icon>
          {{item.name}}
          <!-- <span class="line"></span> -->
        </p>

      </li>
    </ul>
  </div>
</template>
<script>
  import { mapState,mapActions} from 'vuex';
  import BScroll from "better-scroll";
  import icon from "../../../icon/icon.vue";
  import bus from '../bus.js';
  import Vue from 'vue';
  // const ERR_OK=0;
  export default {
    name:"menu",
    data:function () {
      return {
        classMap:["decrease","discount","special","invoice","guarantee"],
        ScrollY:0,
        currentIndex:0
      };
    },
    components:{
      "icon":icon
    },
    computed:{
      ...mapState(['goods'])
    },
    created:function () {
      // this.$store.dispatch('getGoods').then(
      //   response=>{
          this.$nextTick(function () {
            this._initScroll();
            this._calculateTop();
          });
      //   }
      // );
    },
    mounted:function () {
      var self=this;
      bus.$on("currentIndex",function (data) {
        self.currentIndex=data;
      });
    },
    methods:{
      _initScroll:function () {
        this.menuScroll=new BScroll(this.$refs.menu,{probeType:3,click:true});
        this.menuScroll.on('scroll',(pos)=>{
          this.ScrollY=Math.abs(Math.round(pos.y));
        });
      },
      _calculateTop:function () {
        let itemList=this.$refs.menu.getElementsByClassName('item-hook');
        this.itmeTop=[];
        for(let i=0;i<itemList.length;i++){
          this.itmeTop.push(itemList[i].offsetTop)
        }
        // console.log(this.itmeTop);
      },
      menuSelect:function (index,event) {
        // 阻止pc端的默认点击事件
        if (!event._constructed){
          return;
        }
        // console.log(index);
        bus.$emit('menuSelect',index);
      }
    },
  }
</script>
<style scoped>
  .menu{
    position: relative;
  }
  .menu .item{
    display: table;
    box-sizing: border-box;
    width: 80px;
    height: 54px;
    padding: 0 12px;
    line-height: 14px;
    font-size: 0;
    background-color: #f3f5f7;
  }
  .menu .item.current{
    position: relative;
    top: -1px;
    background-color: #fff;
    font-weight: 700;
  }
  .menu .item .text{
    display: table-cell;
    vertical-align: middle;
    font-size: 12px;
    color: rgb(24,20,20);
    font-weight: 200;
    line-height: 14px;
    border-bottom: 1px solid rgba(7,17,27,0.1);
  }
  .menu .item .text.current{
    border-bottom: none;
  }
  .menu .item .text .icon{
    vertical-align: -2px;
  }
  /*.menu .item .line{
    display: block;
    width: 56px;
    height: 1px;
    margin-top: 12px;
    margin-bottom: 12px;
    transform: scaleY(0.5);
    background-color: rgba(7,17,27,0.1);
  }*/
</style>



