<template>
  <span class="icon" v-bind:class="[iconName,iconType]"></span>
</template>
<script>
  export default {
    name:"icon",
    props:{
      // 传入的图标大小，默认为1，适应设备尺寸12px
      iconTypeVal:{type:Number,default:1},
      // 传入的图标名称，默认为1，文字为“减”；
      iconNameVal:{type:String,default:1}
    },
    computed:{
      iconType:function () {
        return "icon-type-"+this.iconTypeVal;
      },
      iconName:function () {
        return "icon-name-"+this.iconNameVal;
      }
    }
  }
</script>
<style>
  .icon{
    display: inline-block;
    background-size: 100% 100%;
    background-repeat: no-repeat;
  }
  .icon.icon-type-1,.icon.icon-type-3{
    width: 12px;
    height: 12px;
  }
  .icon.icon-type-2,.icon.icon-type-4{
    width: 16px;
    height: 16px;
  }

  .icon.icon-name-decrease.icon-type-1 {
    background-image: url("./decrease_1@2x.png");
  }
  .icon.icon-name-decrease.icon-type-2 {
    background-image: url("./decrease_2@2x.png");
  }
  .icon.icon-name-decrease.icon-type-3 {
    background-image: url("./decrease_3@2x.png");
  }
  .icon.icon-name-decrease.icon-type-4 {
    background-image: url("./decrease_4@2x.png");
  }

  .icon.icon-name-discount.icon-type-1 {
    background-image: url("./discount_1@2x.png");
  }
  .icon.icon-name-discount.icon-type-2 {
    background-image: url("./discount_2@2x.png");
  }
  .icon.icon-name-discount.icon-type-3 {
    background-image: url("./discount_3@2x.png");
  }
  .icon.icon-name-discount.icon-type-4 {
    background-image: url("./discount_4@2x.png");
  }

  .icon.icon-name-guarantee.icon-type-1 {
    background-image: url("./guarantee_1@2x.png");
  }
  .icon.icon-name-guarantee.icon-type-2 {
    background-image: url("./guarantee_2@2x.png");
  }
  .icon.icon-name-guarantee.icon-type-3 {
    background-image: url("./guarantee_3@2x.png");
  }
  .icon.icon-name-guarantee.icon-type-4 {
    background-image: url("./guarantee_4@2x.png");
  }

  .icon.icon-name-invoice.icon-type-1 {
    background-image: url("./invoice_1@2x.png");
  }
  .icon.icon-name-invoice.icon-type-2 {
    background-image: url("./invoice_2@2x.png");
  }
  .icon.icon-name-invoice.icon-type-3 {
    background-image: url("./invoice_3@2x.png");
  }
  .icon.icon-name-invoice.icon-type-4 {
    background-image: url("./invoice_4@2x.png");
  }

  .icon.icon-name-special.icon-type-1 {
    background-image: url("./special_1@2x.png");
  }
  .icon.icon-name-special.icon-type-2 {
    background-image: url("./special_2@2x.png");
  }
  .icon.icon-name-special.icon-type-3 {
    background-image: url("./special_3@2x.png");
  }
  .icon.icon-name-special.icon-type-4 {
    background-image: url("./special_4@2x.png");
  }

</style>



