<template>
  <div class="rating-select">
    <button type="button" name="button" class="all" v-bind:class="{'active':selectType===2}" v-on:click="select(2,$event)">
      {{desc.all}}
      <span class="count">{{ratings.length}}</span>
    </button>
    <button type="button" name="button" class="positive" v-bind:class="{'active':selectType===1}" v-on:click="select(1,$event)">
      {{desc.positive}}
      <span class="count">{{positives.length}}</span>
    </button>
    <button type="button" name="button" class="negative" v-bind:class="{'active':selectType===0}" v-on:click="select(0,$event)">
      {{desc.negative}}
      <span class="count">{{negatives.length}}</span>
    </button>
    <div class="toggle-content" v-bind:class="{'on':onlyContent}" v-on:click.stop.prevent="toggleContent">
      <button type="button" name="button" class="toggle-content-btn">
        <span class="icon icon-check_circle"></span>
        只看有内容的评价
      </button>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  const ALL = 2;
  const POSITIVE = 1;
  const NEGATIVE = 0;
  export default{
    props:{
      desc:{
        type: Object,
        default: function () {
          return {
            all:'全部',
            positive:'推荐',
            negative:'吐槽'
          };
        }
      },
      ratings:{
        type:Array,
        default:function () {
          return []
        }
      },
      selectType:{
        type:Number,
        default:ALL
      },
      onlyContent:{
        type:Boolean,
        default:false
      }
    },
    methods:{
      select:function (type,event) {
        if (!event._constructed){
          return;
        }
        this.$emit('select',type)
      },
      toggleContent:function () {
        if (!event._constructed){
          return;
        }
        // 临时解决多次派发click事件问题；
        if (this.lastClick){
          if (event.timeStamp-this.lastClick<100){
            return;
          }
        }
        this.lastClick=event.timeStamp;

        this.$emit('toggleContent')
      }
    },
    computed:{
      positives:function () {
        return this.ratings.filter(rating => rating.rateType === 1);
      },
      negatives:function () {
        return this.ratings.filter(rating => rating.rateType === 0);
      }
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
  .rating-select{
    box-sizing:border-box;
    margin-top:18px;
    button{
      outline:none;
      padding:8px 12px;
      margin-right:8px;
      border:none;
      border-radius:1px;
      font-size:12px;
      color:rgb(77, 85, 93);
      line-height:16px;
    }
    span.count{
      font-size:8px;
    }
    .all{
      background-color:rgba(0, 160, 220, 0.2);
      &.active{
        color:rgb(255, 255, 255);
        background-color:rgb(0, 160, 220);
      }
    }
    .positive{
      background-color:rgba(0, 160, 220, 0.2);
      &.active{
        color:rgb(255, 255, 255);
        background-color:rgb(0, 160, 220);
      }
    }
    .negative{
      background-color:rgba(77, 85, 93, 0.2);
      &.active{
        color:rgb(255, 255, 255);
        background-color:rgb(77, 85, 93);
      }
    }
    .toggle-content{
      margin-top:18px;
      padding:0 0 12px 0;
      font-size:12px;
      color:rgb(147, 153, 159);
      &:before{
        content:'';
        display:block;
        width:100%;
        height:1px;
        margin-bottom:12px;
        border:none;
        background-color:rgba(7, 17, 27, 0.1);
        transform:scaleY(0.5);
      }
      .toggle-content-btn{
        display:flex;
        padding:0;
        align-items:center;
        border-radius:1px;
        background-color:rgba(255, 255, 255, 1);
        .icon{
          display:inline-block;
          margin:0 4px 0 0;
          font-size:24px;
          line-height:24px;
          color:rgb(147, 153, 159);
        }
      }
      &.on{
        .icon{
          color:#00c850;
        }
      }
    }
  }
</style>



