<template>
  <div class="header">
    <div class="background" >
      <img v-bind:src="seller.avatar" alt="">
    </div>
    <div class="content-wrapper">
      <div class="avatar">
        <img v-bind:src="seller.avatar">
      </div>
      <div class="content">
        <div class="brand">
          <h1><span><img src='./brand@2x.png' alt=""></span>{{seller.name}}</h1>
          <p class="description">{{seller.description}}/{{seller.deliveryTime}}分钟送达</p>
          <div v-if="seller.supports" class="supports clearfix">
            <p>
              <span class="icon" v-bind:class="classMap[seller.supports[0].type]"></span>{{seller.supports[0].description}}
            </p>
            <div v-on:click.stop.prevent="detailsShow" class="more">
              {{seller.supports.length}}个&nbsp;><span></span>
            </div>
          </div>
        </div>
      </div>
    </div>

    <!-- 详情弹窗 -->
    <div class="details" v-show="detailsPageShow">
      <div class="seller-title">
        <h1 class="seller-name">{{seller.name}}</h1>
        <div class="seller-star">
          <star v-bind:size="48" v-bind:score="seller.score"></star>
        </div>
      </div>

      <div class="seller-discount">
        <div class="title">
          <span class="line"></span><h2>优惠信息</h2><span class="line"></span>
        </div>
        <ul class="content">
          <li v-for="item in seller.supports">
            <span v-bind:class="classMap[item.type]" class="icon"></span>
            <p class="text">{{item.description}}</p>
          </li>
        </ul>

      </div>

      <div class="seller-bulletin">
        <div class="title">
          <span class="line"></span><h2>商家公告</h2><span class="line"></span>
        </div>
        <div class="content">
          {{seller.bulletin}}
        </div>


      </div>
      <button v-on:click.prevent="detailsClose" type="button" name="button" class="details-close-btn">X</button>
    </div>

    <!-- 公告区 -->
    <div class="bulletin">
      <div class="bulletin-wrapper" v-if="seller.bulletin">
        <span class="icon"></span>
        <p class="text">{{seller.bulletin}}</p>
        <span class="icon-keyboard_arrow_right"></span>
      </div>
    </div>
  </div>
</template>
<script>
  import {mapState,mapActions} from 'vuex';
  import star from "../star/star.vue";
  export default {
    name:"header",
    components:{
      star:star
    },
    data:function () {
      return {
        detailsPageShow:false,
      }
    },
    // props:{
    //   seller:{default:''}
    // },
    created:function () {
      return this.classMap=["decrease","discount","special","invoice","guarantee"];
    },
    methods:{
      detailsClose:function () {
        this.detailsPageShow=false;
      },
      detailsShow:function () {
        this.detailsPageShow=true;
      },
    },
    computed:{
      ...mapState(['seller'])
    },
    // created:function () {
    //   this.$store.dispatch('getSeller');
    // }
  }
</script>
<style scoped>
.clearfix::after{
  content: '';
  display: block;
  height: 0;
  visibility: hidden;
  clear: both;
}
.header{
  position: relative;
  overflow: hidden;
}
.header .background{
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: -1;
  filter: blur(10px);
}
.header .background img{
  width: 100%;
  height: 100%;
}
.header .content-wrapper{
  padding: 24px 12px 18px 24px;
  font-size: 0;
  background-color: rgba(7,17,27,0.5);
}
.header .avatar{
  display: inline-block;
  vertical-align: top;
  margin-right: 16px;
  border-radius: 2px;
}
.header .avatar img{
  width: 64px;
  height: 64px;
}
.header .content{
  margin: 4px 0;
  font-size: 14px;
  display: inline-block;
  vertical-align: top;
}
.header .content .brand h1{
  margin-bottom: 8px;
  font-size: 16px;
  color: rgb(255,255,255);
  font-weight: bold;
  line-height: 18px;
}
.header .content .brand h1 img{
  margin-right: 6px;
  width: 30px;
  height: 18px;
  vertical-align: top;
}
.header .content .description{
  margin-bottom: 10px;
  font-size: 12px;
  color: rgb(255,255,255);
  font-weight: 100;
  line-height: 12px;
}
.header .content .supports{
  display: flex;
  align-items: center;
}
.header .content .supports>p{
  float: left;
  font-size: 10px;
  color: rgb(255,255,255);
  font-weight: 100;
  line-height: 12px;
}
.header .content .supports>p .icon{
  display: inline-block;
  width: 12px;
  height: 12px;
  background-size: 100% 100%;
  background-repeat: no-repeat;
  margin-right: 4px;
  vertical-align: top;
}
.header .content .header .content .content-wrapper .content .brand .decrease{
  background-image: url('./decrease_1@2x.png');
}
.header .content .content-wrapper .content .brand .discount{
  background-image: url('./discount_1@2x.png');
}
.header .content .content-wrapper .content .brand .special{
  background-image: url('./special_1@2x.png');
}
.header .content .content-wrapper .content .brand .invoice{
  background-image: url('./invoice_1@2x.png');
}
.header .content .supports .more{
  position: absolute;
  right: 6px;
  padding: 7px 8px;
  height: 24px;
  font-size: 10px;
  line-height: 24px;
  border-radius: 14px;
  color: rgb(255,255,255);
  background-color: rgba(255, 255, 255, 0.2);
}

.header .bulletin{
  position: relative;
  height: 28px;
  padding: 0 12px;
  background-color: rgba(7,17,27,0.7);
}
.header .bulletin-wrapper{
  position: relative;
  height: 28px;
  font-size: 0;
  display: flex;
  align-items: center;
}
.header .bulletin-wrapper .icon{
  display: inline-block;
  width: 22px;
  height: 12px;
  background-size: 100% 100%;
  vertical-align: middle;
  background-image: url('./bulletin@2x.png');
}
.header .bulletin-wrapper .text{
  display: inline-block;
  width: 89%;
  margin-left: 4px;
  margin-right: 4px;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  font-size: 10px;
  color: rgb(255,255,255);
  font-weight: 200;
  vertical-align: middle;
}
.header .bulletin-wrapper .icon-keyboard_arrow_right{
  position: absolute;
  right: 0;
  font-size: 10px;
  color: rgb(255,255,255);
}



/*弹出页*/
.header .details{
  position: fixed;
  z-index: 100;
  display: flex;
  flex-flow: column;
  overflow: auto;
  box-sizing: border-box;
  left: 0;
  top: 0;
  width: 100%;
  min-height: 100vh;
  padding: 64px 36px 32px 36px;
  background-color: rgba(7,17,27,0.8);
}

.header .details .seller-name{
  font: 16px;
  font-weight: 700;
  color: rgb(255,255,255);
  line-height: 16px;
  text-align: center;
  margin-bottom: 16px;
}
.header .details .seller-star{
  height: 24px;
  text-align: center;
  margin-bottom: 28px;
}
.header .details .seller-discount {
  margin-bottom: 28px;
}
.header .details .seller-discount .title{
  display: flex;
  align-items: center;
  margin-bottom: 24px;
}
.header .details .seller-discount h2{
  display: inline-block;
  margin: 0 12px;
  font-size: 14px;
  font-weight: 700;
  color: rgb(255,255,255);
  line-height: 14px;
}
.details .seller-discount .line{
  display: inline-block;
  width: 112px;
  height: 2px;
  flex:1;
  background-color: rgba(255, 255, 255, 0.2);
}
.details .seller-discount .content {
  padding-left: 12px;
}
.details .seller-discount .content li{
  margin-bottom: 12px;
  display: flex;
  align-items: center;
}
.details .seller-discount .content li:last-child{
  margin-bottom: 0;
}
.details .seller-discount .content .icon{
  display: inline-block;
  width: 16px;
  height: 16px;
  background-size: 100% 100%;
  margin-right: 6px;
}
.details .seller-discount .content .decrease{
  background-image: url('./decrease_2@2x.png');
}
.details .seller-discount .content .discount{
  background-image: url('./discount_2@2x.png');
}
.details .seller-discount .content .special{
  background-image: url('./special_2@2x.png');
}
.details .seller-discount .content .invoice{
  background-image: url('./invoice_2@2x.png');
}
.details .seller-discount .content .guarantee{
  background-image: url('./guarantee_2@2x.png');
}
.details .seller-discount .content .text{
  display: inline-block;
  font-size: 12px;
  font-weight: 100;
  color: rgb(255, 255, 255);
  line-height: 12px;
}




/*弹窗公告区*/
.details .seller-bulletin{
  flex:1;
}
.details .seller-bulletin .title{
  display: flex;
  align-items: center;
}

.details .seller-bulletin h2{
  display: inline-block;
  margin: 0 12px;
  font-size: 14px;
  font-weight: 700;
  color: rgb(255,255,255);
  line-height: 14px;
}
.details .seller-bulletin .line{
  display: inline-block;
  flex: 1;
  width: 110px;
  height: 2px;
  background-color: rgba(255, 255, 255, 0.2);
}
.details .seller-bulletin .content{
  margin-top: 24px;
  padding: 0 12px 20px;
  font-size: 12px;
  font-weight: 100;
  color: rgb(255, 255, 255);
  line-height: 24px;
}

.details .details-close-btn{
  border: none;
  background-color: rgba(255, 255, 255, 0);
  font-size: 32px;
  color: rgba(255, 255, 255, 0.5);
}

</style>



