import Vue from 'vue';
import Router from 'vue-router';
import index from '../App.vue';
import goods from '../components/content/goods/goods.vue';
import ratings from '../components/content/ratings/ratings.vue';
import seller from '../components/content/seller/seller.vue';
Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      redirect:{name:'goods'}
    },
    {
      path: '/goods',
      name: 'goods',
      component: goods
    },
    {
      path: '/ratings',
      name: 'ratings',
      component: ratings
    },
    {
      path:'/seller',
      name:'seller',
      component:seller
    }
  ],
  linkActiveClass: 'active'
})



