<template>
  <div class="nav scale-1px">
    <div class="nav-item">
      <router-link to="/goods">商品</router-link>
    </div>
    <div class="nav-item">
      <router-link to="/ratings">评价</router-link>
    </div>
    <div class="nav-item">
      <router-link to="/seller">商家</router-link>
    </div>
  </div>
</template>
<script>
  export default {
    name:"nav"
  }
</script>
<style scoped>
  .nav{
    display: flex;
  }
  .scale-1px{
    position: relative;
    border: none;
  }
  .scale-1px::after{
    content: '';
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 1px;
    background-color: rgba(7,17,27,0.1);
    transform: scaleY(0.5);
    transform-origin: 0 0;
  }
  .nav-item{
    width: 100%;
    height: 40px;
    text-align: center;
    flex: 1;
    line-height: 40px;
  }
  .nav-item .active{
    color:#f00;
  }
</style>



