<template>
  <transition name="move">
    <div class="food-detail" v-show="showFlag" ref="foodDetail">
      <div class="food-content">
        <button v-on:click.stop.prevent="hide" type="button" name="button" class="back" >
          <i class="icon-arrow_lift"></i>
        </button>
        <div class="banner">
          <img v-bind:src="food.image">
        </div>
        <div class="content">
          <div class="title">
            <h1>{{food.name}}</h1>
            <p>
              <span v-show="food.sellCount" class="sellCount">
                月售{{food.sellCount}}份
              </span>
              <span class="rating" v-show="food.rating">
                好评率{{food.rating}}%
              </span>
            </p>
          </div>
          <div class="price">
            <div class="price-wrapper">
              <span class="currentPrice"><i class="symbol">￥</i>{{food.price}}</span>
              <span class="oldPrice" v-show="food.oldPrice">
                ￥{{food.oldPrice}}
              </span>
            </div>

              <div class="buy">
                <transition name="fade" mode="out-in">
                  <button v-on:click.stop.prevent="addToCart($event)"
                  type="button" name="button"
                  v-if="!food.count" class="buy-button">
                    加入购物车
                  </button>

                  <div class="cart-button-wrapper" v-else>
                    <cart-button v-bind="{food:food,goodIndex:goodIndex,foodIndex:foodIndex}">
                    </cart-button>
                  </div>
                </transition>
              </div>


          </div>
        </div>

        <!-- 商品介绍 -->
        <div class="introduction" v-if="food.info">
          <h1 class="intro-title">商品介绍</h1>
          <p class="intro-content">{{food.info}}</p>
        </div>

        <!-- 商品评价 -->
        <div class="rating">
          <div class="rating-header">
            <h1 class="rating-title">商品评价</h1>
            <rating-select v-bind="{ratings:food.ratings,selectType:selectType,onlyContent:onlyContent}"
            v-on:select="select" v-on:toggleContent="toggleContent"
            class="rating-select">
            </rating-select>
          </div>
          <div class="rating-list">
            <ul class="rating-list-wrapper" v-if="food.ratings && food.ratings.length">
              <li class="rating-item" v-for="rating in food.ratings" v-show="needShow(rating.rateType,rating.text)">
                <div class="rating-time">
                  {{rating.rateTime | formatDate}}
                </div>
                <div class="user-info">
                  <span class="username">{{rating.username}}</span>
                  <img v-bind:src="rating.avatar" alt="" class="avatar">
                </div>
                <p class="rating-content">
                  <span class="icon" v-bind:class="rating.rateType===1 ?
                  'icon-thumb_up' : 'icon-thumb_down'"></span>
                  {{rating.text}}
                </p>
              </li>
            </ul>
            <div class="no-rating" v-else>
              暂无商品评论
            </div>
          </div>
        </div>
      </div>
    </div>
  </transition>
</template>
<script type="text/javascript">
  import {mapMutations} from 'vuex';
  import Vue from 'vue';
  import BScroll from 'better-scroll';
  import shopCart from '../shopcart/shopcart.vue';
  import cartButton from '../cartButton/cartButton.vue';
  import ratingSelect from '@/components/ratingSelect/ratingSelect.vue';
  import {formatDate} from '@/common/js/formatDate.js';
  const ALL = 2;
  const POSITIVE = 1;
  const NEGATIVE = 0;
  export default{
    data:function () {
      return {
        showFlag:false,
        selectType:ALL,
        onlyContent:false
      }
    },
    props:{
      food:{},
      foodIndex:0,
      goodIndex:0
    },
    components:{
      'shop-cart':shopCart,
      'cart-button':cartButton,
      'rating-select':ratingSelect
    },
    methods:{
      ...mapMutations(['setCart']),
      show:function () {
        this.showFlag=true;
        this.$nextTick(() => {
          if (!this.scroll){
            this.scroll=new BScroll(this.$refs.foodDetail,{
              click:true
            });
          }else{
            this.scroll.refresh();
          }
        });
      },
      hide:function () {
        this.showFlag=false;
      },
      addToCart:function (event) {
        if (!event._constructed){
          return;
        }

        // 临时解决多次派发click事件问题；
        if (this.lastClick){
          if (event.timeStamp-this.lastClick<100){
            return;
          }
        }
        this.lastClick=event.timeStamp;

        this.setCart({goodIndex:this.goodIndex,foodIndex:this.foodIndex});
      },
      select:function (type) {
        this.selectType = type;
      },
      toggleContent:function () {
        this.onlyContent = !this.onlyContent;
      },
      needShow:function (type,text) {
        if (this.onlyContent){
          if (this.selectType === ALL){
            return text != '';
          }
          return text != '' && this.selectType === type;
        }
        if (this.selectType === ALL){
          return true;
        }
        return this.selectType === type;
      }

    },
    filters:{
      formatDate:function (time) {
        var date = new Date(time);
        return formatDate(date,'yyyy-MM-dd hh:mm:ss');
      }
    }

  }
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
  .food-detail{
    overflow:hidden;
    position:fixed;
    top:0;
    left:0;
    bottom:46px;
    z-index:100;
    width:100vw;
    background-color:#f3f5f7;
    transform:translate3d(0, 0, 0);
    &.move-enter-active, &.move-leave-active{
      transition:all 0.2s linear;
    }
    &.move-enter, &.move-leave-to{
      transform:translate3d(100%, 0, 0);
    }
    .title{
      font-size:14px;
      color:rgb(7, 17, 27);
      line-height:14px;
    }
    .back{
      position:fixed;
      top:10px;
      left:5px;
      width:3em;
      height:3em;
      background-color:rgba(255, 255, 255, 0);
      border:none;
      font-size:1em;
    }
    .banner{
      width:100vw;
      height:100vw;
      img{
        width:100%;
        height:100%;
      }
    }
    .content{
      box-sizing:border-box;
      width:100%;
      padding:18px;
      background-color:#fff;
      border-bottom:1px solid rgba(7, 17, 27, 0.1);
      .title{
        h1{
          font-size:14px;
          font-weight:700;
          color:rgb(7, 17, 27);
          line-height:14px;
          margin-bottom:8px;
        }
        p{
          font-size:10px;
          color:rgb(147, 153, 159);
          line-height:10px;
          margin-bottom:18px;
          .sellCount{
            margin-right:12px;
          }
        }
      }
      .price{
        width:100%;
        display:flex;
        height:24px;
        justify-content:space-between;
        align-items:center;
        font-size:0px;
        .price-wrapper{
          .currentPrice{
            font-size:14px;
            font-weight:700;
            color:rgb(240, 20, 20);
            line-height:24px;
            margin-right:12px;
            .symbol{
              font-style:normal;
              font-size:10px;
              font-weight:normal;
            }
          }
          .oldPrice{
            text-decoration:line-through;
            font-size:10px;
            font-weight:700;
            color:rgb(147, 153, 159);
            line-height:24px;
            .symbol{
              font-weight:normal
            }
          }
        }
        .buy{
          &.fade-enter-active, &.fade-leave-active{
            transition: opacity 2s ease;
          }
          &.fade-enter, &.fade-leave-to{
            opacity:0;
          }
          .buy-button{
            box-sizing:border-box;
            height:24px;
            width:84px;
            padding:6px 12px;
            background-color:rgb(0,160,220);
            border:none;
            border-radius:12px;
            font-size:10px;
            color:rgb(255, 255, 255);
            line-height:12px;
            box-shadow:none;
            outline:none;

          }
        }

      }
    }
    .introduction{
      box-sizing:border-box;
      padding:18px;
      margin:16px 0;
      background-color:#fff;
      border-top:1px solid rgba(7, 17, 27, 0.1);
      border-bottom:1px solid rgba(7, 17, 27, 0.1);
      .intro-title{
        margin-bottom:6px;
      }
      .intro-content{
        margin:6px 0 0 8px;
        font-size:12px;
        font-weight:200;
        color:rgb(77, 85, 93);
        line-height:24px;
      }
    }
    .rating{
      .rating-header{
        box-sizing:border-box;
        margin-top:16px;
        padding:18px 18px 0 18px;
        background-color:#fff;
        border-bottom:1px solid rgba(7, 17, 27, 0.1);
        .rating-select{
          margin-top:18px;
        }
      }
      .rating-list{
        position:relative;
        padding:0 16px 0 16px;
        background-color:#fff;
        .rating-list-wrapper{
          .rating-item{
            position:relative;
            padding-top:16px;
            /*0.5px 细线*/
            &:after{
              content:'';
              display:block;
              height:1px;
              background-color:rgba(7, 17, 27, 0.1);
              transform:scaleY(0.5);
            }
            .rating-time{
              font-size:10px;
              color:rgb(147, 153, 159);
              line-height:12px;
            }
            .user-info{
              position:absolute;
              top:16px;
              right:18px;
              display:flex;
              align-items:center;
              font-size:0;
              .username{
                font-size:10px;
                color:rgb(147, 153, 159);
                line-height:12px;
                margin-right:6px;
              }
              .avatar{
                width:12px;
                height:12px;
                border-radius:6px;
              }
            }
            .rating-content{
              margin:6px 0 16px 0;
              font-size:12px;
              color:rgb(7, 17, 27)''
              line-height:16px;
              .icon{
                font-size:12px;
                color:rgb(147, 153, 159);
                line-height:24px;
                &.icon-thumb_up{
                  color:rgb(0, 160, 220);
                }
              }
            }
          }
        }
        .no-rating{
          display:flex;
          align-items:center;
          height:40px;
          font-size:12px;
          color:rgb(147, 153, 159);
          line-height:24px;
        }
      }
    }
  }
</style>



