<template>
  <div class="ratings" ref="ratingsHook">
    <div class="ratings-wrapper">
      <!-- 评价概览 -->
      <div class="overview">
        <div class="overview-left">
          <p class="rating-num">{{seller.score}}</p>
          <p class="rating-title">综合评分</p>
          <p class="rank-rate">
            高于周边商家
            <span class="count">{{seller.rankRate}}%</span>
          </p>
        </div>
        <div class="overview-right">
          <div class="service-score">
            <span class="text">服务态度</span>
            <star class="icon" v-bind="{size:36,score:seller.serviceScore}">
            </star>
          </div>
          <div class="food-score">
            <span class="text">商品评分</span>
            <star class="icon" v-bind="{size:36,score:seller.foodScore}"></star>
          </div>
          <div class="delivery-time">
            <span class="text">送达时间</span>
            <span class="count">{{seller.deliveryTime}}分钟</span>
          </div>
        </div>
      </div>

      <!-- 评价内容区 -->
      <div class="content">
        <div class="content-header">
          <rating-select v-bind="{desc:{all:'全部',positive:'满意',negative:'不满意'},ratings:ratings,selectType:selectType,onlyContent:onlyContent}"
          v-on:select="select" v-on:toggleContent="toggleContent">
          </rating-select>
        </div>
        <ul class="content-list">
          <li class="content-list-item" v-for="rating in ratings" v-show="needShow(rating.rateType,rating.text)">
            <div class="user-info clearfix">
              <!-- 用户头像 -->
              <div class="avatar">
                <img v-bind:src="rating.avatar" alt="">
              </div>
              <!-- 用户名及评级 -->
              <div class="title">
                <p class="username">{{rating.username}}</p>
                <div class="user-rating">
                  <star class="icon" v-bind="{size:24,score:rating.score}">
                  </star>
                  <span class="delivery-time">
                    {{rating.deliveryTime}}分钟送达
                  </span>
                </div>
              </div>
              <div class="rating-time">
                {{rating.rateTime | formatDate}}
              </div>

            </div>
            <div class="rating-text">
              <p>{{rating.text}}</p>
            </div>
            <div class="recommend" v-if="rating.recommend && rating.recommend!=''">
              <ul class="recommend-list">
                <span class="icon icon-thumb_up"></span>
                <li v-for="recommendItem in rating.recommend">
                  {{recommendItem}}
                </li>
              </ul>
            </div>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
  import {mapState} from 'vuex';
  import ratingSelect from '@/components/ratingSelect/ratingSelect.vue';
  import shopcart from '@/components/content/goods/shopcart/shopcart.vue';
  import star from '@/components/star/star.vue';
  import BScroll from 'better-scroll';
  import {formatDate} from '@/common/js/formatDate.js';
  const ALL = 2;
  const POSITIVE = 1;
  const NEGATIVE = 0;

  export default {
    data:function () {
      return {
        selectType:ALL,
        onlyContent:false
      }
    },
    components:{
      'rating-select':ratingSelect,
      'shopcart':shopcart,
      'star':star
    },
    computed:{
      ...mapState(['seller','ratings'])
    },
    methods:{
      // ...mapActions(['getSeller','getRatings']),
      select:function (type) {
        this.selectType = type;
      },
      toggleContent:function () {
        this.onlyContent = !this.onlyContent;
      },
      needShow:function (type, text){
        if (this.onlyContent){
          if (this.selectType === ALL){
            return text != '';
          }
          return text != '' && this.selectType === type;
        }
        if (this.selectType === ALL){
          return true;
        }
        return this.selectType === type;
      }
    },
    created:function () {
      this.$store.dispatch('getSeller');
      this.$store.dispatch('getRatings').then(


        response => {
          this.$nextTick( ()=> {
            if (!this.scroll){
              this.scroll = new BScroll(this.$refs.ratingsHook,{
                click:true
              })
            }else {
              this.scroll.refresh();
            }
          })
        }
      );
    },
    filters:{
      formatDate:function (time) {
        var date = new Date(time);
        return formatDate(date,'yyyy-MM-dd hh:mm:ss');
      }
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
  @import "../../../common/stylus/mixin.styl"
  .ratings
    overflow: hidden;
    .ratings-wrapper
      background-color:#f3f5f7
      .overview
        display:flex
        background-color:#fff
        padding:18px 0
        margin-bottom:16px
        .overview-left
          display:flex
          flex-direction:column
          justify-content:center
          align-items:center
          flex:1 1 137px
          width:137px
          border-right:1px solid rgba(7, 17, 27, 0.1)
          .rating-num
            font-size:24px
            color:rgb(255, 153, 0)
            line-height:28px
          .rating-title
            font-size:12px
            /*color:rgb(7, 17, 27)*/
            line-height:12px
            margin:6px 0 8px 0
          .rank-rate
            font-size:10px
            color:rgba(7, 17, 27, 0.7)
            line-height:10px
        .overview-right
          display:flex
          flex-direction:column
          justify-content:center
          align-items:flex-start
          padding-left:24px
          flex:1 1 237px
          width:237px
          .text
            display:inline-block
            margin-right:12px
            font-size:12px;
            color:rgb(7, 17, 27)
            line-height:18px
          .food-score
            margin:8px 0px
          .delivery-time
            .count
              font-size:12px
              color:rgb(147, 153, 159)
              line-height:18px
      .content
        background-color:#fff
        .content-header
          border-bottom:1px solid rgba(7, 17, 27, 0.1)
          padding:18px 18px 0 18px
        .content-list
          padding:0 18px
          .content-list-item
            padding:18px 0
            border-1px(rgba(7, 17, 27, 0.1))
            .user-info
              font-size:0
              .avatar
                display:inline-block
                float:left
                width:28px
                height:28px
                margin-right:12px
                img
                  width:100%
                  height:100%
                  border-radius:50%
              .title
                display:inline-block
                float:left
                .username
                  font-size:10px
                  color:rgb(7, 17, 27)
                  line-height:12px
                  margin-bottom:4px
                .user-rating
                  .delivery-time
                    margin-left:6px
                    font-size:10px
                    font-weight:200
                    color:rgb(147, 153, 159)
                    line-height:12px
              .rating-time
                display:inline-block
                float:right
                font-size:10px
                font-weight:200
                color:rgb(147, 153, 159)
                line-height:12px
            .rating-text
              clear:both
              margin-left:40px
              margin-top:6px
              margin-bottom:8px
              font-size:12px
              color:rgb(7, 17, 27)
              line-height:18px
            .recommend
              margin-left:40px
              .recommend-list
                .icon
                  display:inline-block
                  margin-right:8px
                  font-size:12px
                  color:rgb(0, 160, 220)
                  line-height:16px
                li
                  display:inline-block
                  margin:8px 8px 0 0
                  padding:0 6px 1px
                  border:1px solid rgba(7, 17, 27, 0.1)
                  border-radius:1px
                  font-size:9px
                  color:rgb(147, 153, 159)
                  line-height:16px



</style>



