<template>
  <div class="star" v-bind:class="starType">
    <span v-for="itemClass in itemClasses" v-bind:class="itemClass" class="star-item"></span>
  </div>
</template>
<script>
  export default {
    name:"star",
    props:{
      size:{
        type:Number
      },
      score:{
        type:Number
      }
    },
    computed:{
      starType:function () {
        return "star-"+this.size;
      },
      itemClasses:function () {
        var result=[];
        for (var i=1;i<=Math.floor(this.score);i++){
          result.push('on');
        }
        if (this.score-Math.floor(this.score)>=0.5){
          result.push('half');
        }
        for (var i=result.length;i<5;i++){
          result.push('off');
        }
        return result;
      }
    }
  }

</script>
<style>
  .star {
    font-size: 0;
  }
  .star .star-item{
    display: inline-block;
    background-repeat: no-repeat;
  }
  .star .star-item:last-child{
    margin-right: 0;
  }
  .star-48 .star-item{
    width: 20px;
    height: 20px;
    margin-right: 22px;
    background-size: 100% 100%;
  }
  .star-48 .star-item.on{
    background-image: url('./star48_on@2x.png');
  }
  .star-48 .star-item.half{
    background-image: url('./star48_half@2x.png');
  }
  .star-48 .star-item.off{
    background-image: url('./star48_off@2x.png');
  }

  .star-36 .star-item{
    width: 15px;
    height: 15px;
    margin-right: 6px;
    background-size: 100% 100%;
  }
  .star-36 .star-item.on{
    background-image: url('./star36_on@2x.png');
  }
  .star-36 .star-item.half{
    background-image: url('./star36_half@2x.png');
  }
  .star-36 .star-item.off{
    background-image: url('./star36_off@2x.png');
  }

  .star-24 .star-item{
    width: 10px;
    height: 10px;
    margin-right: 3px;
    background-size: 100% 100%;
  }
  .star-24 .star-item.on{
    background-image: url('./star24_on@2x.png');
  }
  .star-24 .star-item.half{
    background-image: url('./star24_half@2x.png');
  }
  .star-24 .star-item.off{
    background-image: url('./star24_off@2x.png');
  }
</style>



