<template>
  <div class="cartButton">
    <!-- 为删除按钮增加滑动滚动效果 -->
    <transition name="slide-fade">
      <button v-on:click.stop.prevent="removeCartItem($event)"
      type="button" name="button" class="decrease" v-show="food.count>0" >
        <span class="inner icon-remove_circle_outline"></span>
      </button>
    </transition>
    <p class="num" v-show="food.count>0">{{food.count}}</p>
    <button v-on:click.stop.prevent="addCartItem($event)"
    type="button" name="button" class="increase" >
      <span class="icon-add_circle"></span>
    </button>
  </div>
</template>
<script>
  import {mapMutations} from 'vuex';

  export default {

    name:'cartButton',
    // 使用cartButton组件，必须将food，goodIndex和foodIndex传入
    props:{
      food:{
        type:Object,
        default:{}
      },
      goodIndex:{
        type:Number
      },
      foodIndex:{
        type:Number
      },

    },
    methods:{
      ...mapMutations(['addCart','removeCart','setCart']),
      // ***分发映射的actions
      // addCart_Action(goodIndex,foodIndex){
      //   this.$store.dispatch('addCart_Action',goodIndex,foodIndex)
      // },
      // removeCart_Action(){
      //   this.$store.dispatch('removeCart_Action')
      // },
      // setCart_Action({goodIndex,foodIndex}){
      //   this.$store.dispatch('setCart_Action',{goodIndex,foodIndex});
      // },

      addCartItem(event) {
        if (!event._constructed){
          return;
        }
        // 临时解决多次派发click事件问题；
        if (this.lastClick){
          if (event.timeStamp-this.lastClick<100){
            return;
          }
        }
        console.log(event.timeStamp-this.lastClick);
        this.lastClick=event.timeStamp;

        if (!this.food.count){
          this.setCart({goodIndex:this.goodIndex,foodIndex:this.foodIndex});
        }else{
          this.addCart({goodIndex:this.goodIndex,foodIndex:this.foodIndex})
          return
        }
      },
      removeCartItem:function (event) {
        if (!event._constructed){
          return;
        }

        // 临时解决多次派发click事件问题；
        if (this.lastClick){
          if (event.timeStamp-this.lastClick<100){
            return;
          }
        }
        this.lastClick=event.timeStamp;

        if (!this.food.count) {
          return;
        }else{
          this.removeCart({goodIndex:this.goodIndex,foodIndex:this.foodIndex});
        }
      },
    },

  }

</script>
<style scoped>
  .cartButton {
    display: flex;
    justify-content: space-around;
    align-items: center;
  }
  .cartButton button{
    display: flex;
    justify-content: center;
    align-items: center;
    box-sizing: content-box;
    margin: 0;
    padding: 6px;
    font-size: 0;
    width: 20px;
    height: 20px;
    border-style: none;
    background-color: rgba(255, 0, 0,0);
  }
  .cartButton button span{
    display: inline-block;
    font-size: 20px;
    width: 20px;
    height: 20px;
    line-height: 20px;
  }
  .cartButton button span.icon-remove_circle_outline,.cartButton button span.icon-add_circle{
    color: rgb(19, 142, 232);
  }
  .cartButton button span.inner{
    transition: all 0.4s linear;
    transform: rotate(0);
  }
  .cartButton .num{
    min-width: 2em;
    box-sizing: border-box;
    margin: 0 6px;
    font-size: 10px;
    line-height: 24px;
    text-align: center;
    color: rgb(147,153,159);
  }

  .slide-fade-enter-active, .slide-fade-leave-active{
    transition: all .4s linear;
  }

  .slide-fade-enter, .slide-fade-leave-to{
    transform: translateX(20px);
    opacity: 0;
  }

  .slide-fade-enter .inner, .slide-fade-leave .inner{
    transform: rotate(90deg)
  }



</style>



